/*
Theme Name: Piramid
*/

@font-face {
  font-family: FuturaPTBook;
  src: url("../fonts/FuturaPTBook.otf") format("opentype");
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1060px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

/*.debug{
    .debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
    }*/

body {
  font-family: FuturaPTBook;
}

h1 {
  font-weight: bold;
  color: #000;
  font-size: 52px;
  line-height: 1.15;
}

h2 {
  color: #000000;
  font-size: 32px;
  font-family: FuturaPTBook;
  line-height: 1.55;
  font-weight: 600;
}

.red_text {
  color: #ee4041;
}

p {
  color: #000000;
  font-size: 20px;
  font-family: FuturaPTBook;
  line-height: 1.55;
  font-weight: 400;
}

button {
  cursor: pointer;
  font-weight: bold;
  font-family: FuturaPTBook;
  font-size: 18px;
  height: 60px;
  padding: 0;
}

.header {
  background: #000;
  background: url(../img/galka-ptica-boke.jpg) center center no-repeat;
  background-size: cover;
  height: 740px;
}

.header .tagline {
  display: flex;
  flex-wrap: wrap;
  padding-top: 200px;
}

.header .tagline h1 {
  font-size: 72px;
  font-weight: bold;
  line-height: 1.2;
  letter-spacing: 1px;
  margin-bottom: 95px;
  color: #fff;
  width: calc(83.33333333333334% - 30px);
}

.header .tagline .white {
  background: #fff;
  border: none;
  width: 345px;
  margin-right: 45px;
}

.header .tagline .white:hover {
  background: none;
  border: solid 2px;
  color: #fff;
  transition: 0.2s;
}

.header .tagline .glass {
  background: none;
  border: solid 2px;
  color: #fff;
  width: 255px;
}

.header .tagline .glass:hover {
  background-color: #fff;
  color: #000;
  border-color: #fff;
  transition: 0.2s;
}

.header .arrow {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 127px;
  margin-bottom: 50px;
}

.header .arrow__item {
  width: 40px;
  height: 35px;
}

.useful {
  display: flex;
  justify-content: center;
  text-align: center;
  padding-top: 120px;
  padding-bottom: 60px;
}

.useful .desc {
  flex-basis: 450px;
}

.useful .desc h1 {
  margin-bottom: 15px;
}

.useful__advantages {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 150px;
}

.useful__advantages__item {
  display: flex;
  padding-bottom: 45px;
  flex-basis: 500px;
}

.useful__advantages__item .num {
  border: solid #162950;
  min-width: 54px;
  height: 54px;
  border-radius: 50%;
  margin-right: 30px;
  text-align: center;
}

.useful__advantages__item .num p {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #162950;
  font-size: 28px;
  line-height: 1;
  font-weight: 600;
  height: 100%;
}

.useful__advantages__item .box {
  padding-top: 7px;
}

.useful__advantages__item p {
  font-size: 16px;
  line-height: 1.6;
}

/* Format */

.format {
  background-color: #fafafa;
}

.format .descriptin {
  padding: 105px 0 60px;
  text-align: center;
}

.format__box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 105px;
}

.format__box__item {
  flex-basis: 300px;
  text-align: center;
  margin-bottom: 45px;
}

.format__box__item img {
  width: 90px;
  height: 90px;
  margin-bottom: 20px;
}

.format__box__item p {
  font-size: 16px;
  line-height: 1.6;
}

/* Format closed */

/* Result  */

.result {
  display: flex;
  flex-wrap: wrap;
  padding-top: 105px;
  justify-content: center;
}

.result__desc {
  text-align: center;
}

.result__desc p {
  font-size: 20px;
  line-height: 1.55;
  max-width: 760px;
  padding-top: 10px;
}

.result__box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 105px;
}

.result__box__video {
  flex-basis: 480px;
  padding: 50px 0;
}

.result__box__video iframe {
  height: 300px;
  width: 100%;
}

.result__box__video h2 {
  font-size: 28px;
  line-height: 1.2;
  font-weight: 600;
}

.result__box__video p {
  font-size: 16px;
  line-height: 1.6;
  font-weight: 400;
}

/* Result closed */

/* gradient */

.gradient {
  display: flex;
  margin-bottom: 90px;
}

.gradient .left {
  background-image: linear-gradient(left, #b8b8b8, rgba(184, 182, 184, 0));
  min-width: 50%;
  height: 2px;
}

.gradient .right {
  background-image: linear-gradient(right, #b8b8b8, rgba(184, 182, 184, 0));
  min-width: 50%;
  height: 2px;
}

/* gradient closed */

.record h1 {
  text-align: center;
  margin-bottom: 60px;
}

.box__rec {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 0 auto;
  max-width: 780px;
  margin-bottom: 90px;
  /**/
}

.box__rec > div:nth-child(even) {
  margin-left: 2px;
}

.box__rec > div:nth-child(odd) {
  margin-right: 2px;
}

.box__rec > div:nth-child(even) .box__rec__item {
  float: right;
  border-left: solid 2px;
}

.box__rec > div:nth-child(even) .box__rec__item h2,
.box__rec > div:nth-child(even) .box__rec__item p {
  padding-left: 40px;
  padding-bottom: 10px;
}

.box__rec > div:nth-child(even) .box__rec__item h2 {
  line-height: 1.1;
}

.box__rec > div:nth-child(odd) .box__rec__item {
  float: left;
  text-align: end;
  border-right: solid 2px;
}

.box__rec > div:nth-child(odd) .box__rec__item h2,
.box__rec > div:nth-child(odd) .box__rec__item p {
  padding-right: 40px;
}

.box__rec p {
  font-size: 16px;
}

.box__rec__item {
  margin-bottom: 0px;
  width: 50%;
}

.box__rec .circle {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  position: absolute;
}

.box__rec .circle .group {
  background: #162950;
  width: 24px;
  height: 24px;
  border-radius: 100%;
}

.button {
  text-align: center;
  margin-bottom: 50px;
}

.button__style {
  background: #162950;
  color: #fff;
  border: none;
  width: 345px;
}

.button__style:hover {
  background-color: #ee4041;
  transition: 0.5s;
}

.news {
  display: flex;
  padding-bottom: 135px;
  max-width: 560px;
  margin: 0 auto;
}

.news__cancel {
  display: inline;
}

.news__cancel img {
  max-width: 40px;
  margin-right: 20px;
}

.news__desc {
  padding-top: 5px;
}

.news__desc h2 {
  font-size: 28px;
  line-height: 1.15;
  font-weight: 600;
  margin-bottom: 10px;
}

.news__desc h2 .grey {
  opacity: 0.4;
}

.news__desc p {
  font-size: 16px;
  line-height: 1.55;
  font-weight: 400;
  max-width: 440px;
}

/*     answer      */

.answer {
  background-color: #fafafa;
  /*    accordion   */
  /*    accordion   */
}

.answer__desc {
  text-align: center;
  padding: 90px 0 75px;
}

.answer__accordion {
  padding-bottom: 60px;
  max-width: 760px;
  margin: 0 auto;
}

.answer .heading-primary {
  font-size: 2em;
  padding: 2em;
  text-align: center;
}

.answer .accordion dl:after,
.answer .accordion-list:after {
  content: "";
  display: block;
  width: 100%;
}

.answer .accordion dd,
.answer .accordion__panel {
  background-color: #fafafa;
  font-size: 1em;
  line-height: 1.5em;
}

.answer .accordion p {
  padding: 1em 1em 1em 1em;
  font-size: 16px;
  max-width: 500px;
}

.answer .accordion {
  position: relative;
  background-color: #fafafa;
}

.answer .container {
  max-width: 960px;
  margin: 0 auto;
  padding: 2em 0 2em 0;
}

.answer .accordionTitle,
.answer .accordion__Heading {
  background-color: #fafafa;
  text-align: left;
  font-weight: normal;
  font-size: 24px;
  padding: 27px;
  display: block;
  text-decoration: none;
  color: #000;
  transition: background-color 0.5s ease-in-out;
  border-bottom: 1px solid #eee;
  border-top: 1px solid #eee;
}

.answer .accordionTitle:before,
.answer .accordion__Heading:before {
  content: "+";
  font-size: 2em;
  line-height: 0.5em;
  float: right;
  transition: transform 0.3s ease-in-out;
}

.answer .accordionTitleActive:before,
.answer .accordionTitle.is-expanded:before {
  transform: rotate(-225deg);
}

.answer .accordionItem {
  height: auto;
  overflow: hidden;
  max-height: 50em;
  transition: max-height 1s;
}

.answer .accordionItem.is-collapsed {
  max-height: 0;
}

.answer .no-js .accordionItem.is-collapsed {
  max-height: auto;
}

.answer .animateIn {
  animation: accordionIn 0.45s normal ease-in-out both 1;
}

.answer .animateOut {
  animation: accordionOut 0.45s alternate ease-in-out both 1;
}

@keyframes accordionIn {
  0% {
    opacity: 0;
    transform: scale(0.9) rotateX(-60deg);
    transform-origin: 50% 0;
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes accordionOut {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0;
    transform: scale(0.9) rotateX(-60deg);
  }
}

/*     answer  closed    */

/* footer */

footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 40px 0;
}

footer .logo {
  max-width: 180px;
  width: 100%;
}

footer .copywrite p {
  font-size: 16px;
}

footer .social img {
  max-width: 18px;
  margin: 0 5px;
}

@media screen and (min-width: 48em) {
  .answer .accordionItem {
    max-height: 15em;
    transition: max-height 0.5s;
  }
}

@media screen and (max-width: 1100px) {
  .wrapper {
    padding-left: undefined;
    padding-right: undefined;
  }

  .useful__advantages {
    justify-content: center;
  }

  .useful__advantages__item {
    justify-content: center;
    flex-basis: 80%;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: undefined;
    padding-right: undefined;
  }

  .header .tagline h1 {
    font-size: 62px;
  }

  .header .tagline .white {
    width: 280px;
  }

  .header .tagline .glass {
    width: 280px;
  }

  .format__box {
    justify-content: space-around;
  }

  .result__box {
    justify-content: space-around;
  }

  .result__box__video {
    flex-basis: 100%;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 560px) {
  .wrapper {
    padding-left: undefined;
    padding-right: undefined;
  }

  .header .tagline {
    flex-direction: column;
    align-items: center;
  }

  .header .tagline h1 {
    font-size: 42px;
    width: calc(100% - 30px);
  }

  .header .tagline .white {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .useful__advantages__item .num {
    margin-right: 15px;
    min-width: 34px;
    height: 34px;
  }

  .useful__advantages__item .num p {
    font-size: 20px;
  }

  .useful__advantages__item .box {
    padding-top: 0px;
  }

  .result__box__video iframe {
    height: 250px;
  }

  .box__rec {
    padding-left: 11px;
  }

  .box__rec > div:nth-child(even) {
    margin-left: 0px;
  }

  .box__rec > div:nth-child(even) .box__rec__item {
    float: left;
  }

  .box__rec > div:nth-child(odd) .box__rec__item {
    border-right: none;
    border-left: solid 2px;
    text-align: start;
  }

  .box__rec > div:nth-child(odd) .box__rec__item h2,
  .box__rec > div:nth-child(odd) .box__rec__item p {
    padding-left: 40px;
    padding-bottom: 10px;
  }

  .box__rec > div:nth-child(odd) .box__rec__item h2 {
    line-height: 1.1;
  }

  .box__rec__item {
    margin-bottom: 0px;
    width: 70%;
  }

  .box__rec .circle {
    align-items: end;
    left: 0;
  }

  .button__style {
    width: 100%;
  }

  footer {
    justify-content: space-around;
  }

  footer .social {
    padding: 15px;
  }
}

@media screen and (max-width: 320px) {
  .wrapper {
    padding-left: undefined;
    padding-right: undefined;
  }

  .header .tagline h1 {
    font-size: 32px;
  }

  .result__box__video iframe {
    height: 200px;
  }
}